let shuffle = require('lodash.shuffle')
export default {
  data() {
    return {
      rightNum: 0,
      wrongNum: 0,
      quections: this.generateProblems(),
      problemIdx: 0,
      timer: undefined,
    }
  },
  onLoad() {

  },
  methods: {
    generateProblems() {
      var arr = []
      for (let i = 0; i < 10; i++) {
        var x = parseInt(Math.random() * 10)
        var y = parseInt(Math.random() * 10)
        let res = x + y
        let wrongOptions = [res + 1, res + 2, res + 3, res + 4, res + 5]
        if (res > 2) {
          wrongOptions.push(res - 2)
          wrongOptions.push(res - 1)
        }
        if (res > 4) {
          wrongOptions.push(res - 3)
          wrongOptions.push(res - 4)
        }
        let t = shuffle(wrongOptions)
        console.log(t)
        let tmp = t.slice(0, 3)
        let result = parseInt(Math.random() * 4)
        tmp.splice(result, 0, res)
        let options = tmp
        arr.push({
          problem: `${x} + ${y} = ?`,
          result: result,
          options: tmp
        })
      }
      return arr
    },
    handleClick(t, result) {
      console.log(t, result)
      if (t === result) {
        this.playAudio('static/audios/correct.mp3')
        this.rightNum += 1
      } else {
        this.playAudio('static/audios/error.mp3')
        this.wrongNum += 1
      }
      if (this.problemIdx >= this.quections.length - 1) {
        uni.showModal({
          title: '测试结果',
          content: `您答对了${this.rightNum}题,答错${this.wrongNum}题`,
          showCancel: true,
          cancelText: '取消',
          confirmText: '重来',
          success: res => {
            this.reset()
          },
          fail: () => {},
          complete: () => {}
        });
      } else {
        this.problemIdx += 1
      }
    },
    reset() {
      this.rightNum = 0,
        this.wrongNum = 0,
        this.problemIdx = 0,
        this.quections = this.generateProblems()
    },
    playAudio(src) {
      const innerAudioContext = wx.createInnerAudioContext()
      innerAudioContext.autoplay = true
      innerAudioContext.src = src
      // console.log('play')
    }
  },
  onShareAppMessage: function(options) {
    console.log('分享的代码！！')
    return {
      path: 'pages/index/index',
      title: '这题好难，你能帮帮我吗？'
    }
  }
}
